p {
  color: white;
}

.game h1 {
  font-size: 2rem;
  margin: 3rem;
}

.points span {
  font-weight: bold;
}


h3 {
  color: white;
}

.tip span {
  color: #ecfa00;
  text-transform: uppercase;
}

.wordContainer {
  margin: .75em;
  padding: .75em;
  border: 10px solid #ecfa00;
  display: flex;
  justify-content: center;
}

.letter,
.blankSquare {
  font-size: 50px;
  line-height: 1;
  border: 2px solid #000;
  height: 70px;
  width: 100%;
  text-transform: uppercase;
  background-color: white;
  color: #000;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.letterContainer p {
  margin-bottom: 1.2em;
}

.letterContainer form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.letterContainer input {
  height: 50px;
  width: 50px;
  font-size: 2em;
  text-align: center;
  margin-right: 1em;
}

.wrongLettersContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrongLettersContainer span {
  margin-left: 10px;
  text-transform: uppercase;
  color: #f3e460;
}

@media (min-width: 767px) {
  .wordContainer {
    margin: 1em;
    padding: 1.5em;
    border: 20px solid #ecfa00;
  }

  .letter,
  .blankSquare {
    font-size: 70px;
    line-height: 1.5;
    border: 2px solid #000;
    height: 100px;
    width: 100px;
  }

  .game h1 {
    font-size: 2rem;
    margin: 2rem;
  }
}